.login-container{
    background-color: red;
    width: 100vw;
    height: 800px;

}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center ;
    background-color: rgb(32, 178, 197);
}

.login-content label {
    color:white;
    font-size: 15px;
    margin-top: 15px;
}
.submit-button-container{
    display: flex;
    justify-content: center;
    align-items:center;
    margin-top: 12px;
    margin-bottom: 12px;

}

.submit-button{
    width: 90%;
    height: 30px;
    border-radius: 25px;
    border: none;
    outline: none;
    background: white;
    font-size: 14px;
    font-weight: bold;
}
.submit-button:hover {
    opacity: 0.8;
}

.label-error label{
    font-size: 14px;
    color: red;
    background-color: rgb(202, 98, 98);;
}

.label-error {
    font-size: 12px;
    color: red;
    background: rgb(224, 204, 204);
    width: fit-content;
    height: fit-content;
    padding: 0 5px;
    box-sizing: border-box;
    border-radius: 5px;
    
}

.label-alert{
    font-size: 14px;
    color: red;
    background: rgb(224, 204, 204);
    width: fit-content;
    height: fit-content;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 5px;
    margin-bottom: 10px;
    
}