.input-container {
    width: 100%;
}

.regular-style {
    width: 100%;
    height: 35px;
    border-radius: 25px;
    border: none;
    padding: 0 10px 0 20px;
    box-sizing: border-box;
    color: cadetblue;
    font-size: 14px;
    outline: none;
}

.input-container:placeholder{
    font-size: 14px;
    color: gray;
    opacity: 0.8;
}
.input-error {
    color: red;
    background: rosybrown;
    border: 1px solid red;
    width: 100%;
    height: 35px;
    border: none;
    border-radius: 25px;
    padding: 0 10px 0 20px;
    box-sizing: border-box;
    font-size: 14px;
    outline: none;
    
}

.input-error::placeholder {
    color: rgb(202, 98, 98);
    font-size: 14px;
}